import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AppBar from '@material-ui/core/AppBar';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Typography from '@material-ui/core/Typography';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import data from './data/classes.json'

const styles ={
  header: {
    height: 60,
    lineHeight: '60px',
  }
}

class App extends Component {
  state = {
    gymClasses: [],
    selectedClasses: []
  }

  componentDidMount() {
    const gymClasses = data;
    this.setState({ gymClasses });
    this.setState({selectedClasses: [] });

  }

  handleChange = name => event => {
    const newArraySelected = name;
    if (this.state.selectedClasses.includes(name)){
      const newArrayUnselected = this.state.selectedClasses.filter(selectedClass => {
        return selectedClass !== name
      });
      this.setState({ selectedClasses: [...newArrayUnselected] });
    }else{
      this.setState({ selectedClasses: [...this.state.selectedClasses, newArraySelected] });
    }
  };

  render() {
    const gymClasses = this.state.gymClasses;
    const { classes } = this.props;
    const arraydays = gymClasses.filter(gymClass =>
     gymClass.days.some(gymDay => this.state.selectedClasses.includes(gymDay.day))
    );
    return (
      <Grid justify="center" container spacing={16}>
        <Grid item xs={12}>
          <AppBar classes={{root: classes.header}} position="static">Totum Classes</AppBar>
        </Grid>
        <Grid item xs={9}>
        <FormGroup row>
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Monday}
                onChange={this.handleChange('Monday')}
                value="Monday"
              />
            }
            label="Monday"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Tuesday}
                onChange={this.handleChange('Tuesday')}
                value="Tuesday"
              />
            }
            label="Tuesday"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Wednesday}
                onChange={this.handleChange('Wednesday')}
                value="Wednesday"
              />
            }
            label="Wednesday"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Thursday}
                onChange={this.handleChange('Thursday')}
                value="Thursday"
              />
            }
            label="Thursday"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Friday}
                onChange={this.handleChange('Friday')}
                value="Friday"
              />
            }
            label="Friday"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Saturday}
                onChange={this.handleChange('Saturday')}
                value="Saturday"
              />
            }
            label="Saturday"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={this.state.Sunday}
                onChange={this.handleChange('Sunday')}
                value="Sunday"
              />
            }
            label="Sunday"
          />
        </FormGroup>
          { arraydays.map(gymClass => 
            <ExpansionPanel key={gymClass.id}>
              <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                <Typography>{gymClass.name}</Typography>
              </ExpansionPanelSummary>
              <ExpansionPanelDetails>
                <Typography>
                  {gymClass.description}
                </Typography>
              </ExpansionPanelDetails>
            </ExpansionPanel>
          )}
        </Grid>
      </Grid>
    );
  }
}

export default withStyles(styles)(App);



